<template lang="html">
  <b-row class="d-flex justify-content-center align-items-center">
    <b-col md="10" lg="7">
      <div v-if="!(isReg)" class="login">
        <b-form v-on:submit="registration">

          <label for="firstname">Имя: </label>
          <b-input id="firstname"  v-model="firstname" placeholder="Введите своё имя" />

          <label for="lastname">Фамилия: </label>
          <b-input id="lastname" v-model="lastname" placeholder="Введите свой Фамилию" />

          <label for="email">Email: </label>

          <b-input id="email" v-model="email" placeholder="Введите свою почту" />

          <label for="login">Логин: </label>
          <b-input id="login" v-model="login" placeholder="Введите свой логин" />

          <label type="password" for="password">Пароль: </label>
          <b-input type="password" v-model="password" id="text-password"
                   placeholder="Введите свой пароль"              />

          <b-form-text id="password-help-block">
            Только латиница, от 8 до 16 символов
          </b-form-text>

          <b-button block variant="primary" type="submit">Войти</b-button>
        </b-form>
      </div>
      <h1 v-else>Поздравляю! Вы зарегистрированы!</h1>
    </b-col>
  </b-row>
</template>

<script>
export default {
  data() {
    return {
      firstname: '',
      lastname: '',
      email: '',
      login: '',
      password: ''
    }
  },
  computed: {
    isAuth() {
      return this.$store.getters.get_isAuth;
    },
    isReg() {
      return this.$store.getters.get_isReg;
    },

    emailIsValid() {
      return /^[-\w.]+@([A-z0-9][-A-z0-9]+\.)+[A-z]{2,4}$/.test(this.email)
    },
    passIsValid() {
      return /^[a-zA-Z0-9]{8,16}$/.test(this.password)
    },
    logIsValid() {
      return /^[a-zA-Z0-9]+$/.test(this.login)
    }
  },

  methods: {
    async registration() {
      if (this.emailIsValid && this.logIsValid && this.passIsValid) {
        var firstname = this.firstname;
        var lastname = this.lastname;
        var login = this.login;
        var password = this.password;
        var email = this.email;

        var requests = this.post(
              'http://ractyfree.ddns.net:180/api?users.regUser',
              {
                username: login, password: password,
                email: email, first_name: firstname, last_name: lastname
              }
            ); await requests;
      } else {
        alert('Перепроверьте данные, пожалуйста.')
      }
    }
  }
}
</script>

