export default {
  install(Vue, store, cookies, router) {
    Vue.prototype.$myMethod = function () {
      console.log('Be happy my dear friend!');
    },
    Vue.prototype.get = (api_url, options) => {
      Vue.axios.get(api_url, options)
      .then((response) => {
          store.getters.get_isAuth
          console.log(response)
        })
      .catch(function (error) { console.log(error); })
    },
    Vue.prototype.post = (api_url, args, headers) => {
      Vue.axios.post(api_url, args, headers)
      .then((response) => {
          if (api_url == 'http://ractyfree.ddns.net:180/api?users.loginUser') {
            if (response.data.error) {
              alert(response.data.error);
            } else {
              store.commit('set_Token', response.data.token);
              store.commit('set_isAuth', true);

              cookies.set('TOKEN', response.data.token);
              cookies.set('isAuth', true);
              cookies.set('username', args.username);

              router.push('user/' + args.username);
            }
          }

          if (api_url == 'http://ractyfree.ddns.net:180/api?users.regUser') {
            if (response.data.error) {
              alert(response.data.error);
            } else {
              store.commit('set_isReg', true);
              router.push('login');
            }
          }

          if (api_url == 'http://ractyfree.ddns.net:180/api?users.getUser') {
            if (response.data.error) {
              if (response.data.error == 'User not found') {
                store.commit('set_userInfo', {
                  first_name: 'Неизвестен',
                  last_name: 'Неизвестный',
                  username: 'None',
                  email: 'Зачем тебе неизвестность?'
                });
              } else { alert(response.data.error); }
            } else {
              store.commit('set_userInfo', response.data);
            }
          }

          if(api_url == 'http://ractyfree.ddns.net:180/api?projects.getAllProjects') {
            if (response.data.error) {
              alert(response.data.error);
            } else {
              store.commit('set_userInfo', response.data);
            }
          }

          if(api_url == 'http://ractyfree.ddns.net:180/api?projects.makeProject') {
            if (response.data.error) {
              alert(response.data.error);
            } else {
              alert('Проект успешно создан!')
            }
          }

          if(api_url == 'http://ractyfree.ddns.net:180/api?projects.getUserProjects') {
            if (response.data.error) {
              alert(response.data.error);
            } else {
              store.commit('set_userProj', response.data );
            }
          }
        })
      .catch(function (error) { console.log(error) })
    }
  }
}
