<template lang="html">
  <b-row>
    <b-col class="d-flex justify-content-center align-items-center" md="10" lg="5">
      <b-avatar class="avatar" variant="primary" v-bind:text="userInfo.username"></b-avatar>
    </b-col>
    <b-col class="info" md="10" lg="7">
      <h3>{{ userInfo.first_name + ' ' + userInfo.last_name + ' (' + userInfo.username + ')'}}</h3>
      <hr>
      <h6>{{ userInfo.email  }}</h6>
      <hr>
      <ul>
        <li v-for="(element, index) in this.$store.getters.get_userProj"
            v-bind:key="index">
          <h5>{{ element.name }}</h5>
          <p>{{ element.short_description }}</p>
        </li>
      </ul>
    </b-col>
  </b-row>
</template>

<script>
export default {
  computed: {
    username() {
      return this.$route.params.id
    },
    userInfo() {
      return this.$store.getters.get_userInfo;
    }
  },

  methods: {
    startUserpage() {
      this.post(
        'http://ractyfree.ddns.net:180/api?users.getUser',
        {
          username: this.username
        }
      );
    },
    getUserProjects() {
      this.post(
        'http://ractyfree.ddns.net:180/api?projects.getUserProjects',
        {
          username: this.username
        }
      );
    }
  },

  watch: {
      '$route.params': {
          handler(newValue) {
              const { username } = newValue

              this.startUserpage( username )
              this.getUserProjects( username )
          },
          immediate: true,
      }
  }
}
</script>

