import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username: '',
    isAuth: false,
    isReg: false,
    Token: '',
    userInfo: {  },
    userProj: [  ]
  },

  getters: {
    get_Token(state) {
      return state.Token;
    },

    get_userProj(state) {
      return state.userProj;
    },

    get_username(state) {
      return state.username;
    },

    get_userInfo(state) {
      return state.userInfo;
    },

    get_isAuth(state) {
      return state.isAuth;
    },

    get_isReg(state) {
      return state.isReg;
    }
  },

  mutations: {
    set_userProj(state, value) {
      state.userProj = value;
    },

    set_username(state, value) {
      state.username = value;
    },
    set_Token(state, token) {
      state.Token = token;
    },
    set_isAuth(state, value) {
      state.isAuth = value;
    },

    set_isReg(state, value) {
      state.isReg = value;
    },
    set_userInfo(state, value) {
      state.userInfo = value;
    }
  },
  actions: {
  },
  modules: {
  }
})
