<template lang="html">
  <b-row class="d-flex justify-content-center align-items-center">
    <b-col md="10" lg="7">
      <div v-if="!(isAuth)" class="login">
        <b-form v-on:submit="login">
          <label for="login">Логин: </label>
          <b-input id="login" v-model="lgin" placeholder="Введите свой логин" />

          <label type="password" for="password">Пароль: </label>
          <b-input type="password" v-model="pswrd" id="text-password"
                   placeholder="Введите свой пароль"              />

          <b-form-text id="password-help-block">
            Ваш пароль может быть любым, но нужно ли вам это? *Крик злобного брутфорса*
          </b-form-text>

          <b-button block variant="primary" type="submit">Войти</b-button>
        </b-form>
      </div>
      <h1 v-else>Вы уже вошли, не так ли?</h1>
    </b-col>
  </b-row>
</template>

<script>
  export default {
    data() {
      return {
        lgin: '',
        pswrd: ''
      }
    },

    computed: {
      isAuth() {
        return this.$store.getters.get_isAuth;
      }
    },

    methods: {
      async login() {
        var username = this.lgin;
        var password = this.pswrd;

        var requests = this.post(
              'http://ractyfree.ddns.net:180/api?users.loginUser',
              { username: username, password: password }
            ); await requests;
      }
    }
  }
</script>

