<template lang="html">
  <b-row class="d-flex justify-content-center align-items-center">
    <b-col md="10" lg="7">
      <div v-if="isAuth">
        <h1>Вы правда хотите выйти?</h1>
        <b-button block variant="outline-primary" type="submit"
                  v-on:click="logout"                         >
          Да, я хочу выйти
        </b-button>
      </div>
      <h1 v-if="!(isAuth)">Поздравляем! Вы вышли из аккаунта.</h1>
    </b-col>
  </b-row>
</template>

<script>
export default {
  computed: {
    isAuth() {
      return this.$store.getters.get_isAuth;
    }
  },

  methods: {
    logout() {
      this.$store.commit('set_Token', '');
      this.$store.commit('set_isAuth', false);

      this.$cookies.remove('TOKEN');
      this.$cookies.remove('isAuth');
    }
  }
}
</script>

