<template lang="html">
  <b-row class="d-flex justify-content-center align-items-center">
    <b-col md="10" lg="7">
      <div v-if="isAuth" class="create">
        <b-form v-on:submit="createProj">
          <label for="name">Название проекта: </label>

          <b-input
            id="name"
            v-model="name"
            placeholder="Введите наименование проекта"
          />

          <label for="short-descr">Краткое описание проекта: </label>

          <b-form-textarea
            id="short-descr"
            v-model="shortDescr"
            placeholder="введите краткое описание вашего проекта"
            rows="6"
            maxlength="140"
            no-resize
          />
          <b-form-text id="short-descr-help-block">
            Разрешается использование до 140 символов.
          </b-form-text>

          <label for="descr">Полное описание проекта: </label>

          <b-form-textarea
            id="descr"
            v-model="descr"
            placeholder="введите полное описание вашего проекта"
            rows="6"
            no-resize
          />
          <b-form-text id="descr-help-block">
            Без ограничений на количество символов.
          </b-form-text>

          <label for="link">Содержание проекта: </label>
          {{ linkIsValid }}
          <b-input
            id="link"
            v-model="link"
            placeholder="Введите ссылку на готовый проект"
          />

          <b-form-text id="text-help-block">
            Google docs, Onlyoffice, Dropbox и т.д.
          </b-form-text>

          <b-button block variant="primary" type="submit">Войти</b-button>
        </b-form>
      </div>
      <h1 v-else>Вы уже вошли, не так ли?</h1>
    </b-col>
  </b-row>
</template>

<script>
export default {
  data() {
    return  {
      name: '',
      shortDescr: '',
      descr: '',
      link: ''
    }
  },
  computed: {
    isAuth() {
      return this.$store.getters.get_isAuth;
    },
    linkIsValid() {
      var expression = /^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w.-]+)+[\w\-._~:/?#[\]@!$&'()*+,;=.]+$/
      return expression.test(this.link)
    }
  },

  methods: {
    async createProj() {
      if (this.linkIsValid) {
        var config = {
          headers: {
            TOKEN: this.$store.getters.get_Token
          }
        };

        var name = this.name;
        var shortDescr = this.shortDescr;
        var descr = this.descr;
        var link = this.link;

        var requests = this.post(
              'http://ractyfree.ddns.net:180/api?projects.makeProject',
              {
                name: name, short_description: shortDescr,
                description: descr, text: link
              },
              config
            ); await requests;
      } else {
        alert('Извините, но вы ввели неверную ссылку.')
      }
    }
  }
}
</script>

